














































import { Component, Watch, Vue } from 'vue-property-decorator';
import CString from '@/components/links/CString.vue';
import CSelectorSample from '@/components/CSelectorSample.vue';
import encording from 'encoding-japanese';
import obtainUnicode from 'obtain-unicode';

@Component({
  components: {
    CString,
    CSelectorSample,
  },
  head: {
    title: {
      inner: '回文',
    },
  },
})
export default class Palindrome extends Vue {
  // Data
  private dataLinkTitle: string = '関連リンク一覧';
  private dataText: string = '';
  private dataSamples: {
    name: string,
    caption: string,
    content: string,
  }[] = [
    {
      name: 'none',
      caption: '',
      content: '',
    },
    {
      name: 'kitsutsuki-even',
      caption: '啄木鳥',
      content: 'キツ',
    },
    {
      name: 'konomama-even',
      caption: 'このママの子',
      content: 'このま',
    },
    {
      name: 'takeyabu-odd',
      caption: '竹藪焼けた',
      content: 'たけやぶ',
    },
    {
      name: 'tashikani-kashita-odd',
      caption: '確かに貸した',
      content: 'たしかに',
    },
    {
      name: 'tashikani-kashita-odd',
      caption: '私、負けましたわ',
      content: 'わたしまけ',
    },
  ];
  private dataSampleIndex: number = 0;
  private dataRadioGroupName: string = 'even-or-odd';
  private dataRadios: {
    value: string,
    lavel: string,
  }[] = [
    {
      value: 'odd',
      lavel: '奇数',
    },
    {
      value: 'even',
      lavel: '偶数',
    },
  ];
  private dataSelectedRadio: string = 'odd';

  // Computed
  get palindrome() {
    let reverses: string[] = this.dataText.split('').reverse();
    if (0 < reverses.length && this.dataSelectedRadio == 'odd') {
      reverses = reverses.slice(1);
    }
    return this.dataText + reverses.join('');
  }

  // Methods
  private listenOnSampleSelect($event: Event): void {
    this.dataText = this.dataSamples[Number($event)].content;
    this.dataSelectedRadio = this.dataSamples[Number($event)].name.split('-').slice(-1)[0];
  }
  private listenInput($event: Event): void {
    this.dataText = (<HTMLInputElement>(<any>$event).target).value;
  }
}
